import React, { Component } from 'react';
import { Link } from 'gatsby';

import styled from 'styled-components';
import MEDIA_QUERIES from 'src/consts/media-queries';

const Navbar = styled.div`
  color: #111a44;
  margin-left: -2.5em;
  line-height: 1.4;
  width: 100%;

  &.isMenuOpen{
    ${MEDIA_QUERIES.TABLET_ONLY} {
      display: none;
    }
  }  

  ${MEDIA_QUERIES.HD_MIN} {
    margin-left: -3.0em;
  }

  ${MEDIA_QUERIES.LAPTOP} {
    margin-left: -4.4em;
  }
  
  ${MEDIA_QUERIES.TABLET_ONLY} {
    display: none;
  }
  
  ${MEDIA_QUERIES.MOBILE} {
    display: none;
  }
`

const NavigationList = styled.ul`
  list-style: none;
  text-align: left;
  padding: 0;
  margin: 0;

  li {
    display: inline-block;
    margin-right: 31px;

    ${MEDIA_QUERIES.LAPTOP} {
      margin-right: 13px;
    }

  }
`

class PrimaryNavigationBar extends Component {
  render () {
    let { primaryNavData } = this.props;

    return (
      <Navbar>
        <NavigationList>
          {
            (primaryNavData.length > 0) &&
            primaryNavData.map(link => <li key={link.path}>
              <Link to={link.path}
                activeStyle={{fontWeight:700, color: "#000"}}>
                {link.text}
              </Link>
            </li>)
          }
        </NavigationList>
      </Navbar>
    )
  }
}

export default PrimaryNavigationBar;
