import React, { Component } from 'react';
import { connect } from 'react-redux';

import styled from 'styled-components';
import MEDIA_QUERIES from 'src/consts/media-queries';
import VARIABLES from 'src/consts/variables';

import EosioLogoSmallSVG from 'src/assets/EosioLogoSmallSVG';

import HamburgerButton from './components/HamburgerButton';
import DocSearch from './components/DocSearch';
import PrimaryNavigationBar from './components/PrimaryNavigationBar';

const StyledEosioLogoSmallSVG = styled(EosioLogoSmallSVG)`
  width: auto;
  height: 48px;
  display: none;
  margin-top: -24px;

  ${MEDIA_QUERIES.TABLET} {
    display: block;
    height: 41px;
    margin-top: 1px;
    vertical-align: middle;
    body.browser-ie11 &{
      width: 36px;
    }
  }
`;

const Wrapper = styled.div`
  position: fixed;
  width: 83%;
  background: white;
  border-bottom: 1px solid #f0efef;
  font-weight: 500;
  z-index: 1;

  ${MEDIA_QUERIES.LAPTOP} {
    width: 90%;
  }

  ${MEDIA_QUERIES.TABLET} {
    width: 100vw;
    border-bottom: 1px solid #000000;
  }

  ${MEDIA_QUERIES.MOBILE} {
    width: 100vw;
    border-bottom: 1px solid #000000;
  }
`;

const Container = styled.div`
  margin: 0 auto;
  padding: 0 100px;
  max-width: 1566px;
  height: ${VARIABLES.HEIGHT_HEADER_DESKTOP - 1}px;

  ${MEDIA_QUERIES.TABLET} {
    padding: 0 30px;
  }

  ${MEDIA_QUERIES.MOBILE} {
    padding: 0 22px;
    height: ${VARIABLES.HEIGHT_HEADER_MOBILE - 1}px;
  }

  display: flex;
  align-items: center;

`;

const Inner = styled.div`
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;

  width: 100%;
  height: 100%;

  ${MEDIA_QUERIES.MOBILE} {
    flex-wrap: wrap;
  }

  .ButtonHamburger {
    display: none;
    ${MEDIA_QUERIES.TABLET} {
      display: block;
    }
  }
`

const HamburgerMenuText = styled.span`
  display: none;

  &.isMenuOpen {
    display: none;
  }

  ${MEDIA_QUERIES.TABLET} {
    display: block;
    font-size: 13px;
    font-weight: 600;
    margin-right: 28px;
  }

  ${MEDIA_QUERIES.MOBILE} {
    margin-right: 15px;
  }

`

const DocSearchWrapper = styled.div`
  &.isMenuOpen{
    ${MEDIA_QUERIES.TABLET} {
      display: none;
    }
  }
`

class Header extends Component {
  render() {

    let { orgName, repoName, currentVersion, hidesVersion, 
      showSearch, hasPrimaryNavigation, primaryNavData } = this.props;
    if (hidesVersion == null)
      hidesVersion = false;

    return (
      <Wrapper className="Header">
        <Container>
          {
            hasPrimaryNavigation &&
            <PrimaryNavigationBar
              className="header-only"
              primaryNavData={primaryNavData}
              />
          }
          <StyledEosioLogoSmallSVG />
          <Inner>  
            <HamburgerButton
              className={`${this.props.isMenuOpen ? "isMenuOpen" : ""}`}
              onIconClick={() => {
                  this.props.toggleHamburgerMenu();
                }
              }
            />
            <HamburgerMenuText
              className={`${this.props.isMenuOpen ? "isMenuOpen" : ""}`}
              onClick={() => {this.props.toggleHamburgerMenu();}}
            >
              Menu
            </HamburgerMenuText>
            {
              (showSearch)
              ? <DocSearchWrapper className={`${this.props.isMenuOpen ? "isMenuOpen": ""}`}>
                  <DocSearch {...{ orgName, repoName, currentVersion }}/>
              </DocSearchWrapper>
              : null
            }
            </Inner>
        </Container>
      </Wrapper>
    )
  }
}

const mapStateToProps = (state, ownProps) => {
  const { navigate } = state;
  return {
    isMenuOpen: navigate.isMenuOpen,
  };
}

const mapDispatchToProps =  dispatch => {
  return { toggleHamburgerMenu: () => dispatch({ type: `NAV_TOGGLE_HAMBURGER_MENU` }) }
}

export default connect(mapStateToProps, mapDispatchToProps)(Header);
