const MenuItemList = styled.ul`
  > ul > li > a, span {
    font-size: 15px;
    font-weight: 500;
  }
  > li > ul {
    padding-left: 13px;
  }
`

  /**
   * Functions for calculating the position of the active link element
   */

const getActiveLinkElement = () => {
  let matches = document.querySelectorAll("a[aria-current='page']");
  if (matches.length > 0) {
    return matches[0];
  } else return null;
}

const locateActiveLinkElement = (
  element, currentYState, currentHeight
) => {
  let location = {
    insideViewport: true,
    yPos: 0,
    boundary: {}
  };

  if (element) {
    const boundary = element.getBoundingClientRect();
    location["boundary"] = boundary;

    if (boundary.top < currentYState) {
      location["insideViewport"] = false;
      location["yPos"] = (boundary.top < currentHeight) ? 0 : boundary.top - currentHeight;
    } else if (boundary.top > (currentYState+currentHeight)) {
      location["insideViewport"] = false;
      location["yPos"] = boundary.top - currentHeight;
    } 
  }

  return location;
}

class Menu extends Component {
  constructor(props) {
    super(props);
    let path = props.location.pathname;
    this.state = {
      scrollingState: "",
      activeLink: path
    };
    this.handleScroll = this.handleScroll.bind(this);
    this.handleMenuScroll = this.handleMenuScroll.bind(this);
  }

  componentDidMount(){
    const currentHeight = (this.wrapperElem || {}).offsetHeight || 0;
    const activeElement = getActiveLinkElement();
    const elementLocation = locateActiveLinkElement(
      activeElement, this.props.menuScrollY, currentHeight
    )
    let activeLinkTarget = this.state.activeLink;
    window.addEventListener('scroll', this.handleScroll);
    window.addEventListener('resize', this.handleScroll);

    let sanitizedLink = activeLinkTarget.replace(/^\/|\/$/g, '');

    if (this.props.pathPrefix !== '/' && 
      this.props.pathPrefix !== '' &&
      activeLinkTarget.includes(this.props.pathPrefix)) {
      let activeLinkSplitter = this.props.pathPrefix.substr(1);
      activeLinkSplitter += "/";
      sanitizedLink = sanitizedLink.split(activeLinkSplitter).join('');
    }

    let activeTree = (
      activeLinkTarget === ""
    ) ? {} : (this.props.isTagged) ? 
    this.getLevelTagged(this.props.sub, sanitizedLink) : this.getLevel(this.props.sub, sanitizedLink);
    
    // Fallback if the page is not found in SUMMARY.json
    if (!activeTree) activeTree = {};

    this.props.storeNavDepth(sanitizedLink, activeTree);

    //In each page load, make Menu scroll to the last scrolled position from the redux store
    //We cannot use state to do this since each Menu component is mounted again in each page load
    if (elementLocation["insideViewport"])
      this.wrapperElem.scrollTop = this.props.menuScrollY;
    else
      this.wrapperElem.scrollTop = elementLocation["yPos"];

    //In each page load, add "scroll" event listener to the Menu
    this.wrapperElem.addEventListener('scroll', this.handleMenuScroll);

    setTimeout(()=>{this.handleScroll()}, 500);
    this.handleAnchorLinks();
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
    window.removeEventListener('resize', this.handleScroll);

    this.wrapperElem.removeEventListener('scroll', this.handleMenuScroll);
  }

  getLevel (data, link, depth = 0, parent = {}, tree = []) {
    if (data && data.length > 0) {
      for (let i = 0; i < data.length; i++) {
        let node;
        let parsedLink = link;
        if (this.props.isCollection) {
          let splitter = this.props.pathData.collectionPath.substr(1);
          let splitPattern = new RegExp(splitter+'(.+)');
          let splitLink = link.split(splitPattern).join('');
          if (splitLink === link && this.props.pathData.filename === 'index.md') {
            return {};
          }
          parsedLink = splitLink.substr(1);
        }
        if (data[i].link && data[i].link.replace(/ /g, '-').includes(parsedLink)) {
          tree[depth] = data[i].link;
          node = data[i];
          return {
            node: node,
            depth: depth,
            parent: parent,
            tree: tree
          };
        }
        tree[depth] = data[i].link;
        let found = this.getLevel(data[i].sub, link, depth + 1, data[i], tree);
        if (found)
          return found;
      }
    }
  }

  getLevelTagged (data, link, depth = 0, parent = {}, tree = []) {
    if (data && data.length > 0) {
      for (let i = 0; i < data.length; i++) {
        let node;
        /**
         * We need to reduce the link to the purest form to make it 
         * compliant with the SUMMARY.json.
         */
        let baseSplitter = "";
        let splitter = "";
        let parsedLink = "";
        if (this.props.importedSummary !== "N/A") {
          let importedSummaryPath = (this.props.importedSummary[0] === "/") ? this.props.importedSummary.substr(1)
          : this.props.importedSummary;
          let splitPattern = new RegExp(importedSummaryPath+'(.+)');
          let versionSplitter = this.props.currentVersion+"/";
          let step1 = link.split(splitPattern).join('');
          parsedLink = step1.split(versionSplitter).join('');
          if (step1 === parsedLink && this.props.pathData.filename === 'index.md') {
            return {};
          }
        } else {
          baseSplitter = this.props.pathData.collectionPath+"/"+this.props.currentVersion;
          splitter = (this.props.isCollection) ? baseSplitter.substr(1)
          : this.props.pathData.rootDirPath;
          let splitPattern = new RegExp(splitter+'(.+)');
          parsedLink = link.split(splitPattern).join('');
          if (parsedLink === link && this.props.pathData.filename === 'index.md') {
            return {};
          }
        }
        parsedLink = parsedLink.substr(1);
        if (data[i].link && data[i].link.replace(/ /g, '-').includes(parsedLink)) {
          tree[depth] = data[i].link;
          node = data[i];
          return {
            node: node,
            depth: depth,
            parent: parent,
            tree: tree
          };
        } 
        tree[depth] = data[i].link;
        let found = this.getLevelTagged(data[i].sub, link, depth + 1, data[i], tree);
        if (found)
          return found;
      }
    }
  }

  /**
   * Handling how the menu will stay in the screen when the menu having different height on desktop
   */
  handleScroll() {
    let currentHeight = (this.wrapperElem || {}).offsetHeight || 0;
    // Handle the situation when this bar is rendered before the footer...
    let footer = document.querySelector('.Footer');
    let boundingClientRect = (footer) ? footer.getBoundingClientRect() : undefined;
    let topBoundaryValue = (boundingClientRect) ? boundingClientRect.top : 297;

    // Determine if the top edge of the footer is overlapping the bottom edge of menu.
    let overlapping = topBoundaryValue - currentHeight < 0;

    this.setState({overlapping});
  }

  /**
   * Set the scrolled position to the redux store
   */
  handleMenuScroll() {
    this.props.setMenuScrollY((this.wrapperElem || {}).scrollTop);
  }

  handleAnchorLinks()
  {
    let listOfAnchors = this.wrapperElem.getElementsByClassName('menu__link');
    for (let anchor of listOfAnchors) {
        anchor.addEventListener('click', () => {
          this.props.toggleHamburgerMenu(false);
        })
    }
  }

  /**
   * We don't know how many nested tiers are in the SUMMARY.json
   * Therefore we must recursively navigate the tree.
   * The link to the page is contained in 'slug' field of the markdown node
   */
  renderSubMenu(children, depthLevel = 0) {
    let { 
      activeIds: { navPath }, repoName,
      nodes, currentVersion, importedSummary,
      pathData, pathKey, isCollection, isTagged
    } = this.props;
    const subMenu = children.map(child => {
        const re = /^(http|https|ftp)/;
        let sanitizedLink = (!child.link) ? ""
          : child.link.replace('.md','').replace(/ /g,"-");
        let trueSlugObject = nodes.filter(node => {
          return node.fields.slug.includes(sanitizedLink) 
            && node.fields.slug.includes(repoName)
            && node.fields.slug.includes(currentVersion) 
            && !node.fields.slug.includes(`SUMMARY`);
        })[0] || null;
        let trueSlug = (trueSlugObject) ? trueSlugObject.fields.slug : sanitizedLink;

        if (!child.title)
          child.title = (' '+child.tit).slice(1);

        if (!trueSlugObject) {
          // It means the slug is not present within the list. Let us overwrite trueSlug with the correct link.
          let collAdjustment = "";
          if (pathData.tagged) {
            collAdjustment = (importedSummary !== 'N/A') ? importedSummary 
              : (isCollection) ? pathData.collectionPath+"/"+currentVersion
              : pathData.rootDirPath;
          } else {
            collAdjustment = (importedSummary !== 'N/A') ? importedSummary
              : (isCollection) ? pathData.collectionPath
              : "";
          }
          if (importedSummary !== pathKey && importedSummary !== "N/A") {
            let diff = pathKey.split(importedSummary).join('');
            diff = diff.substr(1);
            if (trueSlug.includes(diff)) {
              trueSlug = trueSlug.split(diff).join('');
              collAdjustment = (isTagged) ? pathKey+"/"+currentVersion : pathKey+"/";
            }
          }
          if (trueSlug[0] === '/') 
            trueSlug = trueSlug.substr(1);
          trueSlug = collAdjustment+"/"+trueSlug;
        }

        if (trueSlug[0] !== '/' && trueSlug !== "/") trueSlug = "/"+trueSlug;

        const hasSubOptions = (
          child.sub && child.sub.length > 0
        );
        const isTitleOnly = (
          child.title && !child.link
        );
        const isInHistory = (
          navPath && navPath.indexOf(child.link) > -1
        );
        const showChevron = depthLevel === 0 || hasSubOptions;
        const beDown = isInHistory;
        const isPartiallyActive = ({ location: {pathname, hash} }) => {
          let actualPath = (pathname[pathname.length - 1] === '/') ? pathname : pathname+"/";
          let shouldHighlight = trueSlug+"/"+hash === actualPath+hash;
          if (this.props.pathPrefix !== '/' && this.props.pathPrefix !== '') {
            shouldHighlight = this.props.pathPrefix+trueSlug+"/"+hash === actualPath+hash;
          }
          return (shouldHighlight) 
            ? { className: (!showChevron ? 'active-link' : (hasSubOptions) ? 'down-chevron-active' : 'right-chevron-active') }
            : null
        }
        const display = (
            !child.link // Check if this element has an associated slug
            ? <span className={"menu__link" + (!showChevron ? '' : ' down-chevron')}>
                {child.title}
              </span> 
            : !re.test(child.link)
            ? <Link className={"menu__link" +
                (!showChevron ? '' : beDown ? 
                  " down-chevron" : " right-chevron")
                }
                activeClassName={
                  (!showChevron ? 'active-link' : (hasSubOptions) ? 'down-chevron-active' : 'right-chevron-active')
                }
                to={trueSlug}
                getProps={isPartiallyActive}
              > {child.title.replace(/"/g,'')}
              </Link>
            : <a className={"menu__link" + (!showChevron ? '' : ' right-chevron')}
                  href={child.link} target="_blank" rel="noopener noreferrer">
                  {child.title} <FaExternalLinkAlt />
              </a>
        );
        let subSubMenu;
        const nextDepthLevel = depthLevel + 1;
        if ((hasSubOptions && this.state.activeLink.includes(trueSlug)) ||
            (hasSubOptions && isInHistory) ||
            (hasSubOptions && isTitleOnly)) {
          subSubMenu = this.renderSubMenu(child.sub, nextDepthLevel);
        } 
        return (
          <MenuItem key={child.title+"_"+child.link}>
            { display }
            { subSubMenu }
          </MenuItem>
        )
    });

    return (
      <MenuItemList>
        { subMenu }
      </MenuItemList>
    )

  };

  render() {
    let { 
      repoName, pathData, isCollection, currentVersion, 
      currentDDVersion, latestDDVersion, tagsList, isTagged,
      primaryNavData
    } = this.props;
    let link = (isCollection) ? pathData.collectionPath : "/";
    if (pathData.tagged) {
      link = (isCollection) ? pathData.collectionPath+"/"+currentVersion : pathData.rootDirPath;
    }
    return (
      <MenuWrapper ref={e => this.wrapperElem = e}
                    className={"Menu container" +
                          (this.props.isMenuOpen ? ' active' : '') +
                          (this.state.overlapping ? " overlapping" : '')}>
            <StyledEosioLogoSmallSVG />
            <DeveloperText>developers</DeveloperText> 
            <MenuTitle title={repoName}>
              <Link
                className="menu__link"
                to={link}>
                {repoName}
              </Link>
              <MdClose className="menu-close" onClick={()=>this.props.toggleHamburgerMenu(false)} /> 
              <span className="close-text" onClick={()=>this.props.toggleHamburgerMenu(false)}>Close</span>
            </MenuTitle>    
            {
              isTagged &&
              <TagDropdown
                currentDDVersion={currentDDVersion}
                latestDDVersion={latestDDVersion}
                tagsList={tagsList}
                isCollection={isCollection}
                pathData={pathData}
                />
            }
            {
              primaryNavData[0].text !== 'NONE' &&
              <MobileNavigationBar
                primaryNavData={primaryNavData}
              />
            }
            <MenuItemList>
              { this.renderSubMenu(this.props.sub) }
            </MenuItemList>
      </MenuWrapper>
    )
  }

}

const mapStateToProps = ({navigate:{isMenuOpen, menuScrollY, activeIds}}) =>
  ({isMenuOpen, menuScrollY, activeIds})

const mapDispatchToProps = dispatch =>
  bindActionCreators({toggleHamburgerMenu, setMenuScrollY, storeNavDepth}, dispatch)

export default connect(mapStateToProps, mapDispatchToProps)(Menu);
