import React from "react";
import Menu from "./components/Menu";

import { Location } from '@reach/router';

/**
 * Static query to retrieve the contents of SUMMARY.json
 * It will be pre-processed into a nice array of objects
 * We will recursively traverse it to populate the sidebar
 * TODO: Styling
 */

const Sidebar = ({ repoName, summary, nodes, currentVersion, isTagged, 
  isCollection, pathData, pathKey, importedSummary, latestDDVersion,
  currentDDVersion, tagsList, primaryNavData, pathPrefix }) => (
  <Location>
    {
      ({ location }) => {
        return (
          (summary !== {"array": []}) ?
          <div>
             <Menu sub={summary.array} nodes={nodes} location={location} currentVersion={currentVersion} pathKey={pathKey}
              isCollection={isCollection} pathData={pathData} isTagged={isTagged} importedSummary={importedSummary}
              latestDDVersion={latestDDVersion} currentDDVersion={currentDDVersion} tagsList={tagsList} primaryNavData={primaryNavData}
              pathPrefix={pathPrefix}
              {...{repoName}} />
          </div>
          : null
        );
      }
    }
  </Location>
)


export default Sidebar
