import React, { Component } from 'react';
import { Link } from 'gatsby';

import styled from 'styled-components';
import MEDIA_QUERIES from 'src/consts/media-queries';

const Navbar = styled.div`
  color: #111a44;
  line-height: 2.2;
  width: 100%;
  display: none;
  margin-bottom: 5px;
  margin-top: -15px;

  ${MEDIA_QUERIES.TABLET_ONLY} {
    display: block;
  }
  
  ${MEDIA_QUERIES.MOBILE} {
    display: block;
  }
`

const NavigationList = styled.ul`
  list-style: none;
  text-align: left;
  font-size: 15px;
  margin-left: 10px;
  font-weight: 500;
`

class MobileNavigationBar extends Component {
  render () {
    let { primaryNavData } = this.props;

    return (
      <Navbar>
        <NavigationList>
          {
            (primaryNavData.length > 0) &&
            primaryNavData.map(link => <li key={link.path}>
              <Link to={link.path}
                activeStyle={{fontWeight:700, color: "#3892e3"}}>
                {link.text}
              </Link>
            </li>)
          }
        </NavigationList>
      </Navbar>
    )
  }
}

export default MobileNavigationBar;
