import "./App.scss";

import React from "react";
import { Helmet } from 'react-helmet';

import favicon from "src/assets/favicon.ico";

class App extends React.Component {

  render(){
    let { children, orgName, repoName, currentVersion, contentTitle, isTagged } = this.props;
    if (contentTitle !== "") {
      contentTitle += " | "
    }  
    if (!isTagged)
    return (
      <div className="App">
        <Helmet
          titleTemplate="%s | EOSIO Developer Docs"
          defaultTitle="EOSIO Developer Docs"
        >
          <html lang="en" />

          <title>{`${contentTitle} ${repoName} | ${orgName}`}</title>
          <meta charSet="utf-8" />
          <meta httpEquiv="x-ua-compatible" content="ie=edge" />
          <meta
            name="viewport"
            content="width=device-width,initial-scale=1,shrink-to-fit=no,viewport-fit=cover"
          />

          <meta name="description" content={`This is an automatically generated document site of version '${currentVersion}' for ${repoName} in ${orgName}. EOSIO Developer Docs.`} />

          <link rel="icon" type="image/png" href={favicon} />
        </Helmet>
        {children}
      </div>    )
    else
    return (
      <div className="App">
        <Helmet
          titleTemplate="%s | EOSIO Developer Docs"
          defaultTitle="EOSIO Developer Docs"
        >
          <html lang="en" />

          <title>{`${contentTitle} ${repoName} | ${currentVersion} | ${orgName}`}</title>
          <meta charSet="utf-8" />
          <meta httpEquiv="x-ua-compatible" content="ie=edge" />
          <meta
            name="viewport"
            content="width=device-width,initial-scale=1,shrink-to-fit=no,viewport-fit=cover"
          />

          <meta name="description" content={`This is an automatically generated document site of version '${currentVersion}' for ${repoName} in ${orgName}. EOSIO Developer Docs.`} />

          <link rel="icon" type="image/png" href={favicon} />
        </Helmet>
        {children}
      </div>
    )
  }

}

export default App
