import "./DocSearch.scss";

import React, {Component} from 'react';

import config from '../../../../gatsby-config.js';

class DocSearch extends Component {

  initialize() {

    let { ALGOLIA_INDEX_NAME, ALGOLIA_APP_ID, ALGOLIA_SEARCH_KEY } = config.siteMetadata;

    if (this.algoliaInitialized) {
      return
    }

    window.docsearch({
      appId: ALGOLIA_APP_ID,
      apiKey: ALGOLIA_SEARCH_KEY,
      indexName: ALGOLIA_INDEX_NAME,
      inputSelector: '#algolia-doc-search',
      debug:process.env.NODE_ENV === 'development'
    });

    this.algoliaInitialized = true;
  }

  componentWillUnmount() {

  }

  loadAlgoliaJS() {
    this.initialize();
  }

  render () {

    let { ALGOLIA_INDEX_NAME, ALGOLIA_APP_ID, ALGOLIA_SEARCH_KEY } = config.siteMetadata;

    return (
      ALGOLIA_APP_ID && ALGOLIA_SEARCH_KEY && ALGOLIA_INDEX_NAME
        ?
          <form className="DocSearch"
            id="DocSearch"
            onMouseOver={() => this.loadAlgoliaJS()}
            onClick={() => this.loadAlgoliaJS()}
            onSubmit={e => e.preventDefault()}
            >
            <label>
              <input
                id="algolia-doc-search"
                type="text"
                placeholder="Search"
                aria-label="Search"
                required
                />
              <button className="close-key" type="reset"></button>
            </label>
          </form>
        :
        null
    )
  }

}

export default DocSearch;
